:root {
  --bg-color-main: #ffffff;
  --bg-color-primery: #f5f4fa;
  --bg-color-secondary: #2f303a;
  --bg-color-accent: #2196f3;
  --txt-color-main: #212121;
  --txt-color-primery: #757575;
  --txt-color-secondary: #ffffff;
  --txt-color-accent: #2196f3;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: var(--bg-color-main);
  color: var(--txt-color-main);
}

a {
  text-decoration: none;
}

ul {
  list-style-type: none;
  margin: 0px;
  padding: 0px;
}

h1,
h2,
h3,
h4,
p {
  margin: 0px;
}

img {
  display: block;
}

.container {
  width: 1200px;
  padding: 0px 15px;
  margin: 0 auto;
}
.container--center {
  display: flex;
  align-items: center;
}
.container--baseline {
  display: flex;
  align-items: baseline;
}

.section {
  padding-top: 94px;
  padding-bottom: 94px;
}
.section--dark-bg {
  background-color: var(--bg-color-primery);
}
.section--without-pb {
  padding-bottom: 0px;
}

.section__title {
  margin-bottom: 50px;
  font-weight: 700;
  font-size: 36px;
  line-height: 1.17;
  text-align: center;
  letter-spacing: 0.03em;
}

.button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  min-width: 200px;
  padding: 9px 28px;
  font-family: "Roboto", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.88;
  letter-spacing: 0.06em;
  background-color: var(--bg-color-accent);
  color: var(--txt-color-secondary);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
  border-color: transparent;
  border-radius: 4px;
  cursor: pointer;
}

.socials {
  display: flex;
  justify-content: space-between;
}

.socials__link {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  border-radius: 50%;
  color: #afb1b8;
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.socials__link:hover, .socials__link:focus {
  color: var(--txt-color-secondary);
  background-color: var(--bg-color-accent);
}

.socials__link--dark-bg {
  background-color: rgba(255, 255, 255, 0.1);
  color: var(--txt-color-secondary);
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.socials__link--dark-bg:hover, .socials__link--dark-bg:focus {
  background-color: var(--bg-color-accent);
}

.socials__icon {
  fill: currentColor;
}

.close-btn {
  position: absolute;
  top: 8px;
  right: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  padding: 6px;
  background-color: var(--bg-color-main);
  border: 1px solid rgba(0, 0, 0, 0.1);
  border-radius: 50%;
  cursor: pointer;
  transition-property: color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.close-btn:hover, .close-btn:focus {
  color: var(--txt-color-accent);
}

.close-btn__icon {
  fill: currentColor;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.2);
}
.backdrop--is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: 250ms linear 300ms;
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: linear;
  width: 528px;
  min-height: 581px;
  padding: 33px 40px 47px 40px;
  background-color: var(--bg-color-main);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}
.backdrop--is-hidden .modal {
  transform: translate(-50%, -50%) scale(0);
  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: linear;
}

.page-header {
  border-bottom: 1px solid #ececec;
}

.page-header__logo {
  margin-right: 94px;
  color: var(--txt-color-main);
}

.logo {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 1.19;
  letter-spacing: 0.03em;
  padding: 24px 0;
}
.logo__accent {
  color: var(--txt-color-accent);
}

.site-nav {
  display: flex;
  align-items: center;
  margin-right: auto;
}

.site-nav__list {
  display: flex;
}

.site-nav__item:not(:last-child) {
  margin-right: 50px;
}

.site-nav__link {
  position: relative;
  display: flex;
  align-items: center;
  padding: 31px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  color: var(--txt-color-main);
  transition-property: color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.site-nav__link--current {
  color: var(--txt-color-accent);
}
.site-nav__link--current::after {
  position: absolute;
  content: "";
  top: 98%;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: var(--bg-color-accent);
  border-radius: 2px;
}
.site-nav__link:hover, .site-nav__link:focus {
  color: var(--txt-color-accent);
}

.header-contacts__link {
  position: relative;
  display: flex;
  align-items: center;
  padding: 31px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.02em;
  color: var(--txt-color-main);
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.header-contacts__link:hover, .header-contacts__link:focus {
  color: var(--txt-color-accent);
}
.header-contacts__link:first-child {
  margin-right: 50px;
}

.header-contacts {
  display: inline-flex;
  font-style: normal;
  color: var(--txt-color-primery);
}

.header-contacts__icon {
  width: 18px;
  height: 18px;
  margin-right: 10px;
  fill: currentColor;
}

.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 1600px;
  min-height: 600px;
  margin-right: auto;
  margin-left: auto;
  padding-top: 200px;
  padding-bottom: 200px;
  background-color: var(--bg-color-secondary);
  background-image: linear-gradient(rgba(47, 48, 58, 0.4), rgba(47, 48, 58, 0.4)), url("../images/bg-hero.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.hero__title {
  width: 695px;
  margin-bottom: 30px;
  font-weight: 900;
  font-size: 44px;
  line-height: 1.36;
  text-align: center;
  letter-spacing: 0.06em;
  text-transform: uppercase;
  color: var(--txt-color-secondary);
}

.modal-form__text {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 20px;
  line-height: 1.15;
  text-align: center;
  letter-spacing: 0.03em;
}

.input-field {
  position: relative;
  margin-bottom: 28px;
}
.input-field:nth-of-type(3) {
  margin-bottom: 14px;
}

.input {
  width: 100%;
  padding: 11px 42px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  transition-property: border-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.input:focus {
  outline: none;
  border-color: var(--bg-color-accent);
}

.input__label {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.16;
  letter-spacing: 0.01em;
  color: var(--txt-color-primery);
  position: absolute;
  top: 50%;
  left: 42px;
  transform: translateY(-50%);
  transition-property: transform;
  transition-duration: 250ms;
  transition-timing-function: linear;
}
.input:focus + .input__label, .input:not(:placeholder-shown) + .input__label {
  transform: translate(-38px, -40px);
}

.input__icon {
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
  transition-property: fill;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.input:hover ~ .input__icon, .input:focus ~ .input__icon {
  fill: var(--bg-color-accent);
}

.comment {
  width: 100%;
  padding: 12px 16px;
  margin-bottom: 20px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  resize: none;
  transition-property: border-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.comment:focus {
  outline: none;
  border-color: var(--bg-color-accent);
}
.comment::placeholder {
  font-size: 12px;
  line-height: 1.16;
  letter-spacing: 0.01em;
  color: rgba(117, 117, 117, 0.5);
}

.comment__label {
  font-weight: 400;
  font-size: 12px;
  line-height: 1.16;
  letter-spacing: 0.01em;
  color: var(--txt-color-primery);
}

.checkbox-field {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
}

.checkbox__label {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.03em;
  color: var(--txt-color-primery);
}
.checkbox__label::before {
  display: inline-block;
  margin-right: 9px;
  content: " ";
  width: 16px;
  height: 15px;
  border: 2px solid var(--txt-color-main);
  border-radius: 2px;
  transition-property: border-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.checkbox:focus + .checkbox__label::before {
  border-color: var(--bg-color-accent);
}
.checkbox:checked + .checkbox__label::before {
  border-color: var(--bg-color-accent);
  background-color: var(--bg-color-accent);
  background-image: url("../images/icon-check.svg");
  background-origin: border-box;
  background-position: center;
}

.checkbox__link {
  margin-left: 3px;
  text-decoration: underline;
  color: var(--txt-color-accent);
}

.features-list {
  display: flex;
}

.features-list__item {
  width: 270px;
}
.features-list__item:not(:last-child) {
  margin-right: 30px;
}

.features-list__icon-box {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 25px 100px;
  margin-bottom: 30px;
  background-color: var(--bg-color-primery);
}

.features-list__title {
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}

.features-list__desc {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.7;
  letter-spacing: 0.03em;
  color: var(--txt-color-primery);
}

.services-list {
  display: flex;
  justify-content: space-between;
}

.services-list__item {
  position: relative;
}

.services-list__overlay {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 100%;
  padding-top: 27px;
  padding-bottom: 27px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  text-align: center;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  background-color: rgba(47, 48, 58, 0.8);
  color: var(--txt-color-secondary);
}

.team-list {
  display: flex;
  justify-content: space-between;
}

.team-list__content {
  width: 270px;
  padding: 30px 32px;
  background-color: var(--bg-color-main);
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 2px 1px rgba(0, 0, 0, 0.2);
  border-radius: 0px 0px 4px 4px;
}

.team-list__title {
  margin: 10px;
}

.team-list__title,
.team-list__desc {
  font-weight: 500;
  font-size: 16px;
  line-height: 1.19;
  text-align: center;
  letter-spacing: 0.03em;
}

.team-list__desc {
  font-weight: 400;
  margin-bottom: 16px;
  color: var(--txt-color-primery);
}

.clients-list {
  display: flex;
  justify-content: space-between;
}

.clients-list__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 170px;
  height: 90px;
  color: #afb1b8;
  border: 1px solid;
  border-color: #afb1b8;
  border-radius: 4px;
  transition-property: all;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.clients-list__link:hover, .clients-list__link:focus {
  color: var(--bg-color-accent);
  border-color: var(--bg-color-accent);
}

.clients-list__icon {
  fill: currentColor;
}

.page-footer {
  padding-top: 60px;
  padding-bottom: 60px;
  background-color: var(--bg-color-secondary);
}

.page-footer__logo {
  display: inline-block;
  padding: 0;
  margin-bottom: 20px;
  color: var(--txt-color-secondary);
}

.contacts {
  margin-right: 70px;
}

.footer-contacts {
  display: flex;
  flex-direction: column;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.03em;
  color: var(--txt-color-secondary);
}

.footer-contacts__email,
.footer-contacts__phone {
  color: var(--txt-color-primery);
}

.footer-contacts__address,
.footer-contacts__email {
  margin-bottom: 9px;
}

.footer-socials {
  width: 206px;
}

.footer-socials__text {
  margin-bottom: 20px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--txt-color-secondary);
}

.subscribe {
  margin-left: auto;
}

.subscribe__text {
  margin-bottom: 20px;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.14;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  color: var(--txt-color-secondary);
}

.subscribe__input {
  width: 358px;
  padding: 15px;
  margin-right: 12px;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.03em;
  color: rgba(255, 255, 255, 0.6);
  background-color: transparent;
  border: 1px solid rgba(255, 255, 255, 0.3);
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
  border-radius: 4px;
  transition-property: border-color;
  transition-duration: 250ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.subscribe__input:hover, .subscribe__input:focus {
  outline: none;
  border-color: var(--bg-color-accent);
}
.subscribe__input::placeholder {
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  letter-spacing: 0.03em;
  color: rgba(255, 255, 255, 0.6);
}

.subscribe__button {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
}
.subscribe__button::after {
  content: " ";
  display: inline-block;
  width: 24px;
  height: 24px;
  background-image: url("../images/icon-send.svg");
}

